.switch{
    display: inline-block;
    margin-left: 50px;
    width: 60px;
    height: 34px;
    margin-left: 50px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    border-radius: 34px;
    position: relative;
    cursor: pointer;
    width: 60px;
    height: 34px;
    top: -20px;
    left: 20px;
    right: 0;
    bottom: 0;

    background-color: #ccc;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    border-radius: 50%;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: 0.4s;
}


.switch.heavy-traffic input:checked + .slider {
    background-color: rgb(255, 41, 41);
}

.switch.moderate-traffic input:checked + .slider {
    background-color: #fff022;
}

.switch.light-traffic input:checked + .slider {
    background-color: rgb(0, 255, 0);
}

input:checked + .slider:before {
    transform: translateX(26px);
}