body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: hsl(172, 91%, 15%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.node{
    width: 25px;
    height: 25px;
    outline: 1px solid black;
    display: inline-block;
    background-color: white;
}

.node-finish{
    background-color: rgb(255, 135, 37);
}

.node-start{
    background-color: green;
}

.node-block{
    background-color: hsl(172, 91%, 25%);
    outline: 1px solid hsl(172, 91%, 25%);
}

.node-visited{
    background-color: hsl(173, 100%, 6%);
}

.node-visited.node-start{
    background-color: green;
}

.node-visited.node-finish{
    background-color: rgb(255, 135, 37);
}

.node-shortest-path.node-start{
    background-color: green;
}

.node-shortest-path.node-finish{
    background-color: rgb(255, 135, 37);
}

.node-heavy-traffic{
    background-color: rgba(255, 0, 0);
}

.node-moderate-traffic{
    background-color: rgb(255, 230, 0);
}

.node-light-traffic{
    background-color: rgb(21, 255, 0);
}

.node-shortest-path{
    background-color: hsl(170, 100%, 56%);
}

.switch{
    display: inline-block;
    margin-left: 50px;
    width: 60px;
    height: 34px;
    margin-left: 50px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    border-radius: 34px;
    position: relative;
    cursor: pointer;
    width: 60px;
    height: 34px;
    top: -20px;
    left: 20px;
    right: 0;
    bottom: 0;

    background-color: #ccc;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    border-radius: 50%;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: 0.4s;
}


.switch.heavy-traffic input:checked + .slider {
    background-color: rgb(255, 41, 41);
}

.switch.moderate-traffic input:checked + .slider {
    background-color: #fff022;
}

.switch.light-traffic input:checked + .slider {
    background-color: rgb(0, 255, 0);
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
            transform: translateX(26px);
}
:root {
    --clr--neonBlue: hsl(172, 91%, 52%);
}

.grid{
    margin: 180px 0 0;
}
.switchRow{
    position: absolute;
    margin: 120px 0 0;
}
.row{
    margin: -3px;
}

button {
    position: relative;
    display: inline-block;
    margin-left: 80px;
    right: 100px;
    top: 30px;
    font-size: 30px;
    color: hsl(172, 91%, 52%);
    color: var(--clr--neonBlue);
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0);
    border: 3px solid hsl(172, 91%, 40%);
    background-position: center;
    transition: 0.5s;

    text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.5),
    0 0 0.45em hsl(172, 91%, 52%);

    text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.5),
    0 0 0.45em var(--clr--neonBlue);

    box-shadow: 0 0 0.5em 0 hsl(172, 91%, 52%),
    inset 0 0 0.25em 0 hsl(172, 91%, 52%);

    box-shadow: 0 0 0.5em 0 var(--clr--neonBlue),
    inset 0 0 0.25em 0 var(--clr--neonBlue);
}

button:hover {
    box-shadow: 0 0 1em 0 hsl(172, 91%, 52%),
    inset 0 0 0.4em 0 hsl(172, 91%, 52%);
    box-shadow: 0 0 1em 0 var(--clr--neonBlue),
    inset 0 0 0.4em 0 var(--clr--neonBlue);
    transition: 0.5s;
}

button:active {
    background-color: hsl(172, 91%, 52%);
    background-color: var(--clr--neonBlue);
    background-size: 100%;
    transition: 0s;
}
