:root {
    --clr--neonBlue: hsl(172, 91%, 52%);
}

.grid{
    margin: 180px 0 0;
}
.switchRow{
    position: absolute;
    margin: 120px 0 0;
}
.row{
    margin: -3px;
}

button {
    position: relative;
    display: inline-block;
    margin-left: 80px;
    right: 100px;
    top: 30px;
    font-size: 30px;
    color: var(--clr--neonBlue);
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0);
    border: 3px solid hsl(172, 91%, 40%);
    background-position: center;
    transition: 0.5s;

    text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.5),
    0 0 0.45em var(--clr--neonBlue);

    box-shadow: 0 0 0.5em 0 var(--clr--neonBlue),
    inset 0 0 0.25em 0 var(--clr--neonBlue);
}

button:hover {
    box-shadow: 0 0 1em 0 var(--clr--neonBlue),
    inset 0 0 0.4em 0 var(--clr--neonBlue);
    transition: 0.5s;
}

button:active {
    background-color: var(--clr--neonBlue);
    background-size: 100%;
    transition: 0s;
}