.node{
    width: 25px;
    height: 25px;
    outline: 1px solid black;
    display: inline-block;
    background-color: white;
}

.node-finish{
    background-color: rgb(255, 135, 37);
}

.node-start{
    background-color: green;
}

.node-block{
    background-color: hsl(172, 91%, 25%);
    outline: 1px solid hsl(172, 91%, 25%);
}

.node-visited{
    background-color: hsl(173, 100%, 6%);
}

.node-visited.node-start{
    background-color: green;
}

.node-visited.node-finish{
    background-color: rgb(255, 135, 37);
}

.node-shortest-path.node-start{
    background-color: green;
}

.node-shortest-path.node-finish{
    background-color: rgb(255, 135, 37);
}

.node-heavy-traffic{
    background-color: rgba(255, 0, 0);
}

.node-moderate-traffic{
    background-color: rgb(255, 230, 0);
}

.node-light-traffic{
    background-color: rgb(21, 255, 0);
}

.node-shortest-path{
    background-color: hsl(170, 100%, 56%);
}
